$var1: #002244
$var2: #666666
$var3: #2172ba
$var4: #333333
$var5: #000000
$var6: #6244bb
$var7: #4e32a0
$var8: #ffffff

body 
	margin: 0
	padding: 0

.right__main-menu 
	width: 16px
	height: 78px
	line-height: normal
	vertical-align: baseline
	background: rgb(0, 96, 128)
	padding: 10px
	font-size: 12px
	writing-mode: vertical-rl
	transform: rotateZ(180deg)
	letter-spacing: 0
	font-family: "Open Sans"
	color: white
	position: fixed
	right: 0%
	top: 43%
	box-shadow: none

.back-img 
	background-image: url("../../icons/mainBG.jpg")
	background-repeat: no-repeat
	background-size: 100% 900px
	display: flex
	z-index: 0

.back-color 
	background: black
	background: linear-gradient(to bottom, rgba(0, 0, 0, 0.886) 0%, rgba(0, 0, 0, 0.558) 50%, rgba(0,0,0,0) 100%)
	width: 100%
	height: 150px
	position: absolute
	display: flex
	z-index: 1

.main 
	width: 100%
	height: 725px
	display: flex
	flex-direction: column
	align-items: center
	z-index: 2
	&__header 
		width: 1136px
		height: 96px
		display: flex
		flex-direction: row
		justify-content: space-between
		align-items: center
		&_logo 
			width: 250px
			height: 60px
		
		&_right 
			width: 650px
			height: 70px
			display: flex
			flex-direction: column
			justify-content: space-between
			align-items: flex-end
			&-options 
				width: 448px
				display: flex
				flex-direction: row
				justify-content: space-between
				align-items: center
			
			&-option 
				font-family: "Open Sans"
				color: white
				line-height: 16.8px
				font-size: 12px
				align-content: center
				display: flex
			
			&-menu 
				width: 650px
				display: flex
				flex-direction: row
				justify-content: space-between
				&-link 
					font-size: 14px
					color: white
					font-family: "Open Sans"
					line-height: 1.4
					letter-spacing: 1px
					font-weight: bold

	
	&__info 
		width: 1140px
		display: flex
		flex-direction: row
		justify-content: space-between
		margin-top: 40px
		&_left 
			width: 568px
			height: 538px
			background-color: white
			border-radius: 5px
			display: flex
			flex-direction: column
			align-items: center
			&-menu 
				width: 568px
				height: 48px
				display: flex
				flex-direction: row
				justify-content: space-between
				&-name 
					width: 141px
					height: 45px
					font-size: 16px
					font-weight: normal
					font-family: "Open Sans"
					display: flex
					flex-direction: column
					justify-content: center
					text-align: center
					background-color: $var1
					color: white
					&:first-child 
						border-top-left-radius: 5px
						background-color: white
						color: $var1
					
					&:last-child 
						border-top-right-radius: 5px
					
				
			
			&-discr 
				display: flex
				flex-direction: column
				width: 520px
			
		
		&_right 
			width: 280px
			height: 500px
			&-files 
				width: 280px
				height: 300px
				background-color: rgba(0,0,0,0.5)
				opacity: 0.9
				display: flex
				flex-direction: column
				align-items: center
			
		
		&_reklama 
			width: 432px
			height: 132px
			margin-top: 50px
			margin-left: 10px
            
	

.header__Logo 
	width: 250px
	height: 60px

.img__lang 
	width: 16px
	height: 16px
	color: white
	padding-right: 5px

.img__search 
	width: 16px
	height: 16px
	padding-right: 5px

.fa-bell 
	width: 16px
	height: 16px
	padding-right: 5px

.fa-user 
	width: 16px
	height: 16px
	padding-right: 5px

.fa-cart-shopping 
	width: 16px
	height: 16px
	padding-right: 5px

.firstline 
	display: flex
	flex-direction: row
	justify-content: space-between
	border-bottom: 1px solid black
	margin-top: 10px
	width: 450px
	padding-right: 70px
	&__link 
		width: 93.5px
		display: flex
		flex-direction: column
		justify-content: center
		align-items: center
		padding-top: 10px
		padding-bottom: 10px
		color: $var2
		line-height: 1
		font-family: "Open Sans"
		font-size: 12px
		font-weight: bold
		letter-spacing: 1px
		&:hover 
			color: $var1
		
		&:first-child 
			border-bottom: 2px solid $var3
			color: $var1
		
		&:last-child 
			width: 50px
			font-size: 14px
			font-family: "Open Sans"
			color: $var3
			white-space: nowrap
			border-bottom: 1px solid $var3
			padding-bottom: 0%
			margin-bottom: 10px
		
	

.form 
	margin-top: 10px
	width: 520px
	height: 418px
	display: flex
	flex-direction: column
	justify-content: space-between
	&__first 
		display: flex
		flex-direction: row
		justify-content: space-between
		font-size: 14px
		font-family: "Open Sans"
		font-weight: normal
		letter-spacing: normal
		color: $var4
		line-height: 1
		text-align: center
		height: 20px
		align-items: center
		align-content: center
		padding-top: 10px
		&-link 
			height: 20px
			width: 20px
		
	
	&__fromto 
		width: 520px
		height: 46px
		display: flex
		flex-direction: row
		justify-content: space-between
	
	&_name 
		letter-spacing: 1px
		font-weight: 600
		font-family: "Open Sans"
		color: $var5
		font-size: 12px
		line-height: 1
		display: flex
		flex-direction: row
		justify-content: space-between
		&_point 
			width: 252px
		
	
	&__last 
		display: flex
		flex-direction: row
		justify-content: space-between
		padding-bottom: 30px
		padding-top: 20px
		&_info 
			height: 72px
			display: flex
			flex-direction: column
			justify-content: space-between
			&-link 
				color: $var3
				font-size: 14px
				font-family: "Open Sans"
				letter-spacing: 0
				line-height: 1.15
				&::after 
					content: ''
					display: inline-block
					width: 8px
					height: 8px
					margin-left: 2px
					border: solid $var3
					border-width: 0 2px 2px 0
					-webkit-transform: rotate(315deg)
					transform: rotate(315deg)
					width: 6px
					height: 6px
					transition: border-color .4s
				
			
			&-text1 
				color: $var5
				font-size: 12px
				font-family: "Open Sans"
				letter-spacing: 0
				line-height: 1.4
				margin-top: -20px
			
			&-text2 
				color: $var5
				font-size: 12px
				font-family: "Open Sans"
				letter-spacing: 0
				line-height: 1.4
			
		
		&_buttons 
			height: 98px
			display: flex
			flex-direction: column
			justify-content: space-between
			margin-top: -10px
			&-find 
				width: 235px
				height: 42px
				background-color: $var6
				border: 2px solid $var6
				border-radius: 5px
				display: flex
				flex-direction: row
				justify-content: center
				align-items: center
				color: white
				letter-spacing: 0
				font-size: 16px
				font-family: "Open Sans"
				&:hover 
					background-color: $var7
				
			
			&-credits 
				width: 235px
				height: 42px
				background-color: white
				border-radius: 5px
				border: 2px solid $var6
				display: flex
				flex-direction: row
				justify-content: space-evenly
				align-items: center
				color: $var6
				letter-spacing: 0
				font-family: "Open Sans"
			
		
	

#form__fromto-to 
	width: 235px
	height: 46px
	font-family: "Open Sans"
	border: 0
	border-bottom: 1px solid $var1
	font-size: 18px

#form__fromto-from 
	width: 235px
	height: 46px
	font-family: "Open Sans"
	border: 0
	border-bottom: 1px solid $var1
	font-weight: 700
	font-size: 18px

.dateandtravelers 
	display: flex
	flex-direction: row
	justify-content: space-between

#dateandtravelers_date 
	width: 252px
	height: 46px
	border: 0
	font-family: "Open Sans"
	border-bottom: 1px solid $var1
	font-weight: 400
	font-size: 18px

#dateandtravelers_travelers 
	width: 252px
	height: 46px
	font-family: "Open Sans"
	font-size: 18px

#tarif 
	width: 252px
	height: 46px
	font-family: "Open Sans"
	font-weight: 700
	font-size: 18px
	margin-top: 10px

.files__name 
	width: 270px
	margin-top: 15px
	margin-left: 10px
	font-weight: bold
	font-size: 12px
	line-height: 1
	letter-spacing: 1px
	color: white
	font-family: "Open Sans"

.files__text 
	width: 270px
	color: white
	letter-spacing: 0
	font-family: "Open Sans"
	font-size: 25px
	font-weight: normal

.files__learn 
	width: 270px
	margin-left: 10px
	margin-top: 10px
	font-size: 14px
	line-height: 1.15
	letter-spacing: 0px
	color: white
	font-family: "Open Sans"

.files__upr 
	width: 230px
	display: flex
	flex-direction: row
	float: right
	justify-content: space-between
	margin-top: 50px
	&-play 
		width: 10px
		height: 10px
		margin-left: 130px
	
	&-line 
		width: 1px
		height: 20px
		background-color: white
		position: relative
		top: -5px
	
	&-page 
		width: 10px
		height: 10px
	

.header__line 
	display: none
	height: 15px
	width: 2px
	background-color: $var2
	margin-left: 5px

.header__block 
	display: none
	width: 20px
	height: 16px
	flex-direction: column
	justify-content: space-between
	&-line 
		display: none
		width: 20px
		height: 2px
		background-color: white
	

.header__tablet_menu 
	display: none
	color: $var8
	font-size: 12px
	font-weight: 600
	font-family: inherit
	letter-spacing: 0
	align-items: center

.fa-bell 
	color: white

.fa-user 
	color: white

.fa-cart-shopping 
	color: white

.header__tablet_menu 
	display: none

.header__block 
	margin-right: 10px

.files__text 
	width: 95%

.files__name 
	width: 95%

.files__learn 
	width: 95%
